@import '../../assets/css/variables';

.content {
  margin: 40px auto;
  width: 93%;
  display: grid;

  grid-template-columns: 2.5fr 1fr;
  grid-gap: 20px;

  @media (max-width: $mobile) {
    grid-auto-flow: row;
    grid-template-columns: 1fr;
  }


  h3, h4, span, p {
    font-weight: 300;
  }

  .left {

    img {
      display: block;
      margin: auto;
      width: 100%;
      max-height: 73vh;
      object-fit: cover;
    }

    .numbers {
      margin-top: 15px;
      color: #343434;
      padding-left: 62%;
      font-size: .92em;
      display: grid;
      grid-auto-flow: column;
      grid-template-columns: repeat(3, auto);
      justify-items: right;

      @media (max-width: $mobile) {
        grid-template-columns: 1fr 1fr 1fr;
        padding-left: 0;
        justify-items: center;
      }

      h4 {
        margin: 2px 0;
        svg {
          transform: translateY(2px);
        }
      }

      p {
        grid-row: 2;
        grid-column: 1/-1;
      }
    }
  }

  .right {

    .author {
      display: grid;
      grid-auto-flow: column;
      align-items: center;
      justify-items: center;
      padding: 15px 24px;
      background-color: #f9f9f9;
      color: #565656;
      min-height: 130px;

      h3 {
        font-size: 1.2em;
      }

      img {
        max-width: 100px;
        //border-radius: 50px;
      }
    }

    p {
      text-align: right;
      color: #636767;
    }

    .buttons {
      width: 100%;
      display: grid;
      grid-template-columns: 1fr 1fr;

      a, .favorites {
        width: 100%;
        height: 100%;
        background-color: #35bb5d;
        font-size: .96em;
        padding: 12px 7px;
        margin: 0;
        color: white;
        text-align: center;
        text-decoration: none;

        &:hover {
          background-color: #34cc5c;
        }

        svg {
          display: block;
          margin: 10px auto 0 auto;
          transform: scale(1.45);

        }

        &:active, &:visited {
          color: white;
        }
      }

      .favorites {
        background-color: #4271ff;
        cursor: pointer;

        &:hover {
          background-color: #517aff;
        }
      }
    }

    .flash {
      color: #7e7b7f;
    }
  }
}